.App {
  text-align: center;
}
#myVideo {
  width: 100vw;
  height: 100vh;
  object-fit: fill;
  position: absolute;
  right: 0;
  bottom: 0;
}
header{
  width: 100vw;
  height: 100vh;
}
h1{
  color:white;
  position: absolute;
  font-size: 6em;
  margin: 0;
  background-color: rgba(256, 256, 256,0.5);
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
main{
  max-width: 960px;
  min-width: 660px;
  margin: 0 auto;
}
.item{
  height: 200px;
  margin-top: 50px;
  display: flex;
}
.item > div{
  height: 100%;
  margin-right: 50px;
}
.item img{
  min-width: 300px;
  max-width: 300px;
  height: 100%;
}
.item button{
  border: 0;
  font-size: 16px;
}
.modal{
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
}
.modal-bg{
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 4;
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
}
.modal-content{
  width: 660px;
  margin: 0 auto;
  font-size: 30px;
  z-index: 5;
  position: relative;
  background-color: rgba(256, 256, 256, 0.8);
  padding: 50px;
}
.modal-content h2{
  font-size: 30px;
}